@import url(https://fonts.googleapis.com/css?family=Alatsi|Roboto&display=swap);
:root {
  position: relative;
  box-sizing: border-box;
  font-family: Alatsi, sans-serif;
  font-size: 62.5%;
  background-color: #e9e9f0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0px;
  padding: 0px;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

p {
  margin: 0px;
  padding: 0px;
}

tr,
td,
th {
  margin: 0;
  padding: 0;
}

